<template>
  <div class="wind-tab-item" 
       :class="{'wind-tab-selected':currentSelected}"
       @click="onItemClick"
  >
    <slot></slot>
  </div>
</template>

<script type="text/babel">

export default {
  name: 'w-tab-item',
  props: {
    // 待处理红点
    showDot: {
      type: Boolean,
      default: false
    },
    disabled:{
      type: Boolean,
      default: false
    },
    iconClass: String,
    selected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      simple: false,
      hasActiveIcon: false,
      currentIndex: -1,
      currentSelected: this.selected
    }
  },
  methods: {
    onItemClick () {
      if (typeof this.disabled === 'undefined' || this.disabled === false){
        this.currentSelected = true
        this.$parent.currentIndex = this.currentIndex
        this.$nextTick(() => {
            this.$emit('on-item-click', this.currentIndex)
        })
        // console.log(this.currentIndex)
      }
    }
  },
  watch: {
    currentSelected (val) {
      if (val) {
        this.$parent.currentIndex = this.currentIndex
      }
    }
  }
}
</script>

<style>
.wind-tab-item {
  flex:1;
  line-height: 44px;
  font-size:14px;
}
.wind-tab-item.wind-tab-selected {
  color:red;
}

</style>



