<template>
	<div>
		<w-progress :percent="percent" :height="9" animation= "barberpole">{{percent}}%</w-progress>
		<w-progress :percent="percent" :height="9" animation= "barberpole" lineColor="#ef4f4f">{{percent}}%</w-progress>
		<w-progress :percent="percent" :height="9" animation= "barberpole" lineColor="#34c9ac">{{percent}}%</w-progress>
		<w-progress :percent="percent" height="6px" animation= "barberpole" :showCancel="true" @on-cancel="cancel">{{percent}}%</w-progress>
		<w-progress :percent="percent" >
			<span>{{percent}}%</span>
		</w-progress>
		<div class="button-group">
			<w-button type='primary' @click="startPropress">开启进度条</w-button>
		</div>
	</div>
</template>

<script>

export default {
	name: 'ChecListDemo',
	data () {
		return {
			percent: 0
		}
	},
	created () {
	},
	methods: {
		startPropress () {
			if (this.intervalTimer) {
				clearInterval(this.intervalTimer)
			}
			this.percent = 0
			this.intervalTimer = setInterval(() => {
				if (this.percent < 100) {
					this.percent++
				} else {
					if (this.intervalTimer) {
						clearInterval(this.intervalTimer)
					}
				}
			}, 15)
		},
		cancel () {
			if (this.intervalTimer) {
				clearInterval(this.intervalTimer)
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.button-group {
	padding-left:10px;
	padding-right:10px;
	margin-top:10px;
	margin-bottom:10px;
}
</style>



