<template>
	<div class="wind-progress">
		<div class='wind-progress-content' :style="{
				height: progressHeight
			}">
			<div class="wind-progress-line" :style="{
				width: percent + '%',
				backgroundColor: lineColor
			}" :class="animationStyle"></div>      
		</div>
		<slot></slot>
		<a href="javascript:;" v-if="showCancel">
			<i class="icon-wind-cancel wind-progress-cancel" @click="cancel"></i>
		</a>
	</div>
</template>
<script>
/**
 * w-progress by shang 2018/4/6
 * @desc 进度条
 * @param {percent} [Number] - 百分比,渲染进度条的长度
 * @param {showCancel}  [Boolean] - 是否显示停止按钮
 * @param {height} [String,Number] - 进度条高度
 * @param {animation} [String] - 动画样式 可选barberpole
 * @param {lineColor} [String] - 进度条背景颜色
 *
 * @slot - 显示在进度条后 
 * 
 * @event {on-cancel} 点击取消按钮 触发
 *
 * @example
 * <w-progress :percent="percent" height="6px" animation= "barberpole">{{percent}}%</w-progress>
 * <w-progress :percent="percent" >
	 <span>{{percent}}%</span>
   </w-progress>
 */
export default {
	name: 'w-progress',
	props: {
		percent: {
			type: Number,
			default: 0
		},
		showCancel: {
			type: Boolean,
			default: false
		},
		height:[String,Number],
		animation: String,
		lineColor: {
			type: String,
			default: 'var(--progress-line-color)'
		}
	},
	computed: {
		progressHeight() {
			return typeof this.height === 'number' ? `${this.height}px` : this.height
		},
		animationStyle() {
			if (this.animation === 'barberpole') {
				return 'wind-progress-barberpole'
			}
		}
	},
	methods: {
		cancel() {
			this.$emit('on-cancel')
		}
	}
}
</script>

<style lang="css">
.wind-progress {
	width:100%;
	height: 30px;
	line-height: 30px;
	display: flex;
	position:relative;
	align-items: center;
}
.wind-progress .wind-progress-content {
	width:100%;
	height: 3px;
	background-color: #EBEBEB;
	display: flex;
	border-radius:4px; 
}
.wind-progress .wind-progress-content .wind-progress-line {
	width: 0;
	height: 100%;
	border-radius:4px;
}
.wind-progress .wind-progress-cancel {
	font-size: 18px;
    vertical-align: middle;
    color: var(--progress-cancel-color);
}
.wind-progress-barberpole {
	background-image: linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
    background-size: 40px 40px;
	animation: barberpole 12s linear infinite, animate-positive 2s;
}
@keyframes animate-positive{
	0% { width: 0; }
}
@keyframes barberpole {
	from {
		background-position: 0% 0%;
	}
	to {
		background-position: 600px 0%;
	}
}
</style>



