<template>
  <div class="wind-tab">
    <div class="wind-tab-content">
      <div class="wind-tabs">
        <slot></slot>
      </div>
      <div class="wind-tab-link" :class="tabLinkClass" :style="[tabLinkStyle,tabLinkTopStyle]">
        <span class="wind-tab-link-span"></span>
      </div>
    </div>
  </div>
</template>

<script type="text/babel">

export default {
  name: 'w-tab',
  props: {
    value: Number,
    iconClass: String,
    lineWidth: {
      type: Number,
      default: 2
    },
    tabLinkTop:false
  },
  data () {
    return {
      currentIndex: -1,
      number: this.$children.length,
      direction:'forward'
    }
  },
  mounted () {
    if (this.value >= 0) {
      this.currentIndex = this.value
    }
    this.updateIndex()
  },
  methods: {
    // 为子元素生成索引,用于判断选中状态
    updateIndex () {
      if (!this.$children || !this.$children.length) {
        return false
      }
      this.number = this.$children.length
      let children = this.$children
      for (let i = 0; i < children.length; i++) {
        children[i].currentIndex = i
        if (children[i].currentSelected) {
          this.currentIndex = i
        }
      }
    }
  },
  watch: {
    currentIndex (val, oldVal) {
      this.direction = val > oldVal ? 'forward' : 'backward'
      // 清空原选中的子元素选中状态
      oldVal > -1 && this.$children[oldVal] && (this.$children[oldVal].currentSelected = false)
      // 设置当前选中的子元素选中状态
      val > -1 && this.$children[val] && (this.$children[val].currentSelected = true)
      this.$emit('input', val)
      this.$emit('on-index-change', val, oldVal)
    },
    value (val) {
      this.currentIndex = val
    }
  },
  computed: {
    barLeft () {
      const count = this.number
      return `${this.currentIndex * (100/count)}%`;
    },
    barRight () {
      const count = this.number
      return `${(count-this.currentIndex-1) * (100/count)}%`;
    },
    tabLinkClass () {
      return {
        'wind-tab-link-transition-forward':this.direction==='forward',
        'wind-tab-link-transition-backward':this.direction==='backward'
      }
    },
    tabLinkStyle () {
      const tabLinkStyle = {
        left: this.barLeft,
        right: this.barRight,
        display: 'block',
        height: this.lineWidth + 'px'
      }
      return tabLinkStyle
    },
    tabLinkTopStyle () {
      return this.tabLinkTop?{top:0}:'';
    }
  }
}
</script>

<style>
.wind-tab {
  position:relative;
  padding-top: 44px;
}
.wind-tab .wind-tab-content {
  height:44px;
  position:absolute;
  top:0;
  left:0;
  right:0;
}
.wind-tab .wind-tab-content .wind-tabs {
  display: flex;
  height:44px;
  position:relative;
}
.wind-tab .wind-tab-link {
  background-color: red;
  position: absolute;
  /*left: 0%;*/
  /*right:0%;*/
}
.wind-tab .wind-tab-link.wind-tab-link-transition-forward {
  transition:left 0.5s,right 0.3s;
}
.wind-tab .wind-tab-link.wind-tab-link-transition-backward {
  transition:left 0.3s,right 0.5s;
  
}

</style>



